import React from 'react'
import Person from './Person'

const Contacts = ({ people , onClick}) => {
    const rows = () => people.map(person =>
        <Person key={person.name} person={person} onClick={onClick}></Person>
    )
    return (
        <div>
            <ul>
                {rows()}
            </ul>
        </div>
    )
}
export default Contacts