import React from 'react'

const ContactForm = ({ onSubmit, newName, nameChangeHandler, newNumber, numberChangeHandler }) => {
    return (
        <>
            <h3>Add new contact</h3>
            <form onSubmit={onSubmit}>
                <div>
                    name: <input id='name' value={newName} onChange={nameChangeHandler} />
                </div>
                <div>
                    number: <input id='number' value={newNumber} onChange={numberChangeHandler} />
                </div>
                <div>
                    <button type="submit">add</button>
                </div>
            </form>
        </>
    )
}

export default ContactForm 