import React, { useState, useEffect } from 'react'

import Contacts from './components/Contacts'
import Filter from './components/Filter'
import ContactForm from './components/ContactForm'
import personsService from './services/persons'
import Notification from './components/Notification'

const App = () => {
  const [people, setPeople] = useState([])
  const [newName, setNewName] = useState('')
  const [newNumber, setNewNumber] = useState('')
  const [filterText, setFilterText] = useState('')
  const [infoMessage, setInfoMessage] = useState('')
  const [messageType, setMessageType] = useState('success')

  const handleNameChange = event => setNewName(event.target.value)
  const handleNumberChange = event => setNewNumber(event.target.value)
  const handleFilterChange = event => setFilterText(event.target.value)

  const addContact = event => {
    event.preventDefault()
    if (newName !== '' && newNumber !== '') {
      const newPerson = { name: newName, number: newNumber }
      if (people.some(person => person.name === newName)) {
        if (window.confirm(`${newName} has already been added to the phonebook. Replace the old number with a new one?`)) {
          updateContact(newPerson)
        }
      }
      else {
        personsService.create(newPerson).then(createdPerson => {
          setPeople(people.concat(createdPerson))
          showInfoMessage(`Added ${newPerson.name}`, 'success')
        })
      }
    }
  }

  const deleteContact = name => {
    if (window.confirm(`Delete ${name}?`)) {
      const id = people.find(person => person.name === name).id
      personsService
        .remove(id)
        .then(() => setPeople(people.filter(person => person.name !== name)))
    }
  }

  const updateContact = personToUpdate => {
    const id = people.find(person => person.name === personToUpdate.name).id
    personsService
      .update(id, personToUpdate)
      .then(updatedPerson => {
        setPeople(people.map(person => person.name === personToUpdate.name ? updatedPerson : person))
        showInfoMessage(`Updated ${updatedPerson.name}`, 'success')
      }).catch(error => {
        console.log(error)
        showInfoMessage(`Couln't update: information about ${personToUpdate.name} has been removed from the server.`, 'error')
        setPeople(people.filter(person => person.name !== personToUpdate.name))
      })
  }

  const showInfoMessage = (message, messageLevel) => {
    setMessageType(messageLevel)
    setInfoMessage(message)
    setTimeout(() => {
      setInfoMessage(null)
    }, 5000)
  }

  useEffect(() => {
    personsService.getAll()
      .then(initialPeople => setPeople(initialPeople))
  }, [])

  const peopleToShow = () => {
    const regex = new RegExp(`.*${filterText}.*`, 'gi');
    return people.filter(person => person.name.match(regex))
  }

  return (
    <div>
      <h2>Phonebook</h2>
      <Notification message={infoMessage} type={messageType} />
      <Filter text={'filter: '} value={filterText} filterChangeHandler={handleFilterChange} ></Filter>

      <ContactForm
        onSubmit={addContact}
        newName={newName} nameChangeHandler={handleNameChange}
        newNumber={newNumber} numberChangeHandler={handleNumberChange}>
      </ContactForm>

      <h2>Numbers</h2>
      <Contacts people={peopleToShow()} onClick={deleteContact}></Contacts>
    </div>
  )
}

export default App